import React, {useEffect, useState} from "react";
import * as Yup from 'yup';
import {Formik, Form, ErrorMessage, Field} from 'formik'
import {getAllCategories} from "../services/categoryService";
import {addNewBook} from "../services/bookService";
import {useNavigate} from 'react-router-dom'

function AddComponent() {
    const [book, setBook] = useState({
        code: "",
        name: "",
        author: "",
        publicationOfDate: "",
        category: ""
    });
    const [categories, setCategories] = useState([])
    const navigate = useNavigate();
    useEffect(() => {
        const fetchData = async () => {
            let list = await getAllCategories();
            setCategories(list)
        }
        fetchData();
    }, [])
    const handleSubmit = async (value) => {
        console.log(value);
        const newBook = {
            ...value,
            category: JSON.parse(value.category)
        }
        console.log(newBook)
        await addNewBook(newBook);
        navigate('/list');

    }
    const validattion = Yup.object({
        code: Yup.string().required('Yêu cầu nhập'),
        name: Yup.string().required('Yêu cầu nhập'),
        author: Yup.string().required('Yêu cầu nhập'),
        publicationOfDate: Yup.date().required('Yêu cầu nhập'),
        category: Yup.string().required('Yêu cầu nhập')
    })
    return (
        <>
            <h3>Add new book</h3>
            <Formik initialValues={book} onSubmit={handleSubmit} validationSchema={validattion}>
                <Form>
                    <div>
                        <label>Code</label>
                        <Field type={'text'} name={'code'}/>
                        <ErrorMessage component='div' name='code'/>
                    </div>
                    <div>
                        <label>Name</label>
                        <Field type={'text'} name={'name'}/>
                        <ErrorMessage component='div' name='name'/>

                    </div>
                    <div>
                        <label>Author</label>
                        <Field type={'text'} name={'author'}/>
                        <ErrorMessage component='div' name='author'/>
                    </div>
                    <div>
                        <label>Publication of date</label>
                        <Field type={'date'} name={'publicationOfDate'}/>
                        <ErrorMessage component='div' name='publicationOfDate'/>
                    </div>
                    <div>
                        <label>Category</label>
                        <Field as='select' name={'category'}>
                            <option value={""}>---Chọn--------</option>
                            {categories.map(c => (
                                <option key={c.id} value={JSON.stringify(c)}>{c.name}</option>
                            ))}
                        </Field>
                        <ErrorMessage component='div' name='category'/>
                    </div>
                    <div>
                        <button type={'submit'}>Save</button>
                    </div>

                </Form>
            </Formik>
        </>
    )
}

export default AddComponent;