import React, {useEffect, useState} from "react";
import {getAll} from "../services/bookService";
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
function ListComponent() {
    const [list, setList] = useState([]);
    const [page, setPage] = useState(1);
    const [totalPage, setTotalPage] = useState(0);
    const [search, setSearch] = useState({searchName:"",categoryId:""});
    useEffect(()=>{
        const fetchData = async ()=>{
            const  resp = await getAll(page,search,);
            setList(resp.data);
            setTotalPage(()=>{
                const totalCount = resp.headers['x-total-count'];
                return  Math.ceil(totalCount / 2);
            });
        }
        fetchData();
    },[page,search])
    const handleNext =(event)=>{
        event.preventDefault();
        if (page<totalPage){
            setPage(pre=> pre+1);
        }
    }
    const handlePre =(event)=>{
        event.preventDefault();
        if (page>1){
            setPage(pre=> pre-1);
        }
    }
    const handleOnChange =(e)=>{
        setSearch(pre =>({
            ...pre,
            [e.target.name]: e.target.value

        }));
    }
    return (
        <>
            <h1>Danh sách</h1>
            <input onChange={(e)=>{handleOnChange(e)}} name={'searchName'} placeholder={'Search by name'}/>
            <select onChange={(e)=>{handleOnChange(e)}} name={'categoryId'}>
                <option value={''}>-------Chọn---------</option>
                <option value={1}>Khoa học</option>
                <option value={2}>Xã hội</option>
            </select>
            <table className={'table table-dark'}>
                <thead>
                <tr>
                    <th>STT</th>
                    <th>Code</th>
                    <th>Name</th>
                    <th>Author</th>
                    <th>Price</th>
                    <th>Publication</th>
                    <th>Category</th>
                </tr>
                </thead>
                <tbody>
                {list.map((book,i)=>(
                    <tr key={book.id}>
                        <td>{i+1}</td>
                        <td>{book.code}</td>
                        <td>{book.name}</td>
                        <td>{book.author}</td>
                        <td>{book.price}</td>
                        <td>{book.publicationOfDate}</td>
                        <td>{book.category.name}</td>
                    </tr>
                ))}

                </tbody>
            </table>
            <nav aria-label="Page navigation example">
                <ul className="pagination">
                    <li className="page-item">
                        <a onClick={(event)=>{
                            handlePre(event)
                        }} className="page-link" href="#" aria-label="Previous">
                            <span aria-hidden="true">&laquo;</span>
                        </a>
                    </li>
                    {Array.from({ length: totalPage }, (_, index) => (
                        <li
                            key={index + 1}
                            className={`page-item ${page === index + 1 ? 'active' : ''}`} // Đánh dấu trang hiện tại
                        >
                            <a
                                className="page-link"
                                href="#"
                                onClick={(event) => {
                                    event.preventDefault();
                                    setPage(index + 1); // Chuyển trang
                                }}
                            >
                                {index + 1}
                            </a>
                        </li>
                    ))}


                    <li className="page-item">
                        <a onClick={(event)=>{
                            handleNext(event)
                        }} className="page-link" href="" aria-label="Next">
                            <span aria-hidden="true">&raquo;</span>
                        </a>
                    </li>
                </ul>
            </nav>
        </>
    );
}

export default ListComponent;
